import { createStore, applyMiddleware } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension'; // Chrome eklentisini rahata kullanp neler dndn grebileceiz.
import thunk from 'redux-thunk'; // Our middleware
import rootReducer from './reducers'; // We gonna have multiple reducers, and we gonna combine them in rootReducer

const initialState = {};

const middleware = [thunk];

const store = createStore(
  // STORE'umuzu oluturduk.
  rootReducer,
  initialState,
  composeWithDevTools(applyMiddleware(...middleware))
);

export default store;
